@import "../../colors.scss";

.shareWebApp {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  .title {
    display: flex;
    justify-content: center;
    align-items: center;

    h2 {
      padding-left: 20px;
      padding-right: 10px;
      color: $color-white;
      font-size: 14px;
      font-weight: 300;
      justify-content: center;
    }
  }
  .shareWebButtons {
    display: flex;
    justify-items: flex-start;
    align-self: flex-start; /* Postavite ovu vrednost kako biste pomakli shareWebButtons na vrh */
    padding-left: 8px;
    .react-share__ShareButton {
      margin-left: 5px;
      &:hover {
        scale: 1.4;
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .shareWebApp {
    flex-direction: column;
    width: 100%;
    justify-content: center;
    padding-top: 30px;
    margin: 30px;
    .title {
      text-align: center;
      padding-top: 20px;
      margin: 15px;
      h2 {
        text-align: center;
      }
    }
    .shareWebButtons {
      justify-content: space-around;
      padding: 10px;
      .react-share__ShareButton {
        display: flex;
        flex-direction: row;
        margin: 6px;
      }
    }
  }
}
